@font-face {
  font-family: 'S-CoreDream-3Light';
  src: url('https://cdn.jsdelivr.net/gh/projectnoonnu/noonfonts_six@1.2/S-CoreDream-3Light.woff') format('woff');
  font-weight: normal;
  font-style: normal;
}
body {
  margin: 0;
  font-family: 'S-CoreDream-3Light',-apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale; 
  font-weight: 600;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.cursor-pointer{
  cursor: pointer;
}


/*header*/

.logo>img{
  width: 10%;
  max-width: 100px;
  margin-top: -10%;
}
.nav-logo{
  width: 7%;
  margin: 0;
  float: left;
}   


.home-bg{
  width: auto;
  height: 600px;
  background-image: url(../public/img/trip4.jpg);
  background-size: cover;
  background-position: center 70%;
  border-radius: 10px;
  box-shadow: 2px 2px 2px 2px rgba(85, 85, 85, 0.575);
}
.title_cover{
  position: relative;
  margin-top: 10%;
}


.nav-bg{
  border-bottom: 1px solid #b3ceec;
  /* background-color: rgb(233, 239, 244); */
  /* #b3cdec5e */
}
.nav-btn{
  color: #fff;
}
.login-btn{
  text-decoration: none;
  font-size: 0.7em;
  font-weight: 600;
  color: #666;
  margin-top: 10%;
}




/* side-menu */
.side-cover{
  margin-top:7.3%;
}
.side-txt{
  font-size: 0.9em;
  font-weight: 600;
}
.create-btn{
  margin-top: 10%;
  border: 1px solid #b3ceec;
  color: #b3ceec;
}
.create-btn:hover{
  background-color: #b3ceec;
  color: #fff;
  border: 1px solid #b3ceec;    
}
.info-box{
  padding: 1%;
  max-height: 300px;
  border-radius: 10px;
  box-shadow: 2px 2px 2px 1px rgba(194, 193, 193, 0.575);
  border: 1px solid #b3ceec;
  box-sizing: border-box;
}

.body-box{
  padding: 5%;
  min-height: 500px;
  border-radius: 10px;
  box-shadow: 2px 2px 2px 1px rgba(194, 193, 193, 0.575);
  border: 1px solid #b3ceec;
  box-sizing: border-box;
}
.body-box>hr{
  border: 1px solid #b3ceec;
}



.search-bar{
  width: 50%;
  height: 40px;
  margin: 1.5% auto 2%;
  font-size: 0.8em;
  font-weight: 600;
  border: 1px dotted #b3ceec;
}

.card-box{
  width: 45%;
  margin: 1%;
  display: inline-block;
  padding: 1%;
  border: 1px solid #b3ceec;
  /* border: 1px solid rgb(192,244,207); */
  box-shadow: 2px 2px 2px 1px rgba(194, 193, 193, 0.575);
}
.card-header{
  background-color: #b3cdec9d;
  border-bottom : 1px solid #b3ceec;
  /* background-color: rgb(192,244,207);*/
  max-height: 40px; 
  overflow: hidden;
}
.card-title{
  font-size: 1.1em;
  font-weight:800;
  color: rgb(81,80,93);
}
.delete-btn, .edit-btn{
  font-weight: 600;
  color: #666;
}
.delete-btn:hover, .edit-btn:hover{
  color: #dc3545;
}
.card-text{
  font-size: 0.8em;
  font-weight: 600;
  color: #666;
  height: 95px;
  overflow: hidden;
}

.pagination-cover{
  margin-right: 6%;
}

.active>.page-link, .active>.page-link:hover {
  background-color: #b3ceec;
  border-color: #b3ceec;
  color: #fff;
}
.page-link:hover{
  color: #b3ceec;
}

.page-link {
  color: #b3ceec;
  height: 30px;
  font-size: 0.8em;
}

.show-cover{
  margin-top: 3%;
}
.show-title{
  min-width: 50%;
  font-size: 2em;
}
.show-title, .show-body{
  font-family: 'S-CoreDream-3Light';
}
.show-body{
  font-size: 0.9em;
  color: #666;
}


.text-muted{
  font-size: 0.7em;
}


.toasts-btn{
  background-color: #b3ceec;
  border: 1px solid #b3ceec;
  color: #fff;
}

.cancel-btn{
  background-color: #b3ccec8c;
  color: #666;
}
.cancel-btn:hover{
  background-color: #b3ceec;
  color: #fff;
}

